import React from "react";
import { NavLink } from "react-router-dom";
import Friends from "../Friends/Friends";
import s from './Navbar.module.css';


const Navbar = (props) => {
   
   
   
   
    return (


        <nav className={s.nav}>
            <div className={s.item}>
                <NavLink to="/profile" className={s.inactive} exact activeclassname={s.active} >Profile </NavLink>
            </div>

            <div className={s.item}>
                <NavLink to="/dialogs" className={s.inactive} activeclassname={s.active} >Messages</NavLink>
            </div>
            <div className={s.item}>
                <NavLink to="/news" className={s.inactive} activeclassname={s.active}>News</NavLink>
            </div>
            <div className={s.item}>
                <NavLink to="/music" className={s.inactive} activeclassname={s.active} >Music</NavLink>
            </div>
            <div className={s.item}>
                <NavLink to="/settings" className={s.inactive} activeclassname={s.active} > Settings</NavLink>
            </div>
            <div className={s.item}>
                <NavLink to="/users" className={s.inactive} exact activeclassname={s.active} >Users </NavLink>
            </div>
            <div className={s.item}>
                <NavLink to="/login" className={s.inactive} exact activeclassname={s.active} >Login </NavLink>
            </div>
            <div className={s.item}>
                <NavLink to="/friends" className={s.inactive} activeclassname={s.active} > Friends</NavLink>
                <div className={s.friends}>
                    <div>Sasha
                        <div>
                            <img src="data:image/png;base64,"></img>
                        </div>
                    </div>
                    <div>Masha
                        <div>
                            <img src="data:image/png;base64,"></img>

                        </div>
                    </div>
                    <div>Vasja
                        <div>
                            <img src="data:image/png;base64,"></img>
                        </div>
                    </div>
                </div>
            </div>
        </nav>

    )
};
export default Navbar;