import React from "react";
import { useState } from "react";
import styles from './Paginator.module.css';



let Paginator = ({totalItemsCount,pageSize,currentPage,onPageChanged,portionSize=10}) => {

 
    let pagesCount = Math.ceil(totalItemsCount/pageSize);
    let pages = [];
    for (let i = 1; i <= pagesCount; i++) {
        pages.push(i);
    }

    let portionCount = Math.ceil(pagesCount/portionSize);
    let [portionNumber,setPortionNumber] = useState(1);
    let leftPortionPageNumber = (portionNumber-1)*portionSize +1;
    let rightPortionPageNumber= portionNumber*portionSize;

    return (
        <div className={styles.paginator}> 
        {portionNumber>1 && <button onClick={()=>{setPortionNumber(1)}}>First</button>}
        {portionNumber > 1&& <button className={styles.button} onClick={()=>setPortionNumber(portionNumber-1)}>Prev</button>}
         
                {pages
                .filter(p => p >= leftPortionPageNumber && p <=rightPortionPageNumber)
                .map(
                    p => {
                        return <span className={currentPage === p && styles.selectedPage}
                            onClick={(e) => { onPageChanged(p); }} >{p}</span>
                            
                    }
                )}
                {portionCount>portionNumber&&
                <button className={styles.button} onClick={()=>{setPortionNumber(portionNumber+1)}}>Next</button>}
                {pagesCount>portionNumber*portionSize && <button onClick={()=>{setPortionNumber(pagesCount/pageSize)}}>Last</button> }{console.log(pagesCount/pageSize)}{console.log(portionNumber*portionSize)}
        </div>
    )




}
export default Paginator;